import axios from 'axios';

export const getJwtToken = () => {
  console.log(window.sessionStorage)
  if (typeof window !== 'undefined' && window.sessionStorage) {
    return sessionStorage.getItem('jwt');
  }
  return null;
};


export const handleLogout = () => {
  sessionStorage.removeItem('jwt');
  window.location.reload();
};

export const fetchTicket = async () => {
  try {
    const response = await axios.post('http://localhost:3001/api/ticket');
    return response.data.ticket;
  } catch {
    throw new Error('Failed to obtain ticket');
  }
};

export const fetchToken = async (ticket: string) => {
  try {
    const response = await axios.post('http://localhost:3001/api/token', {
      ticketId: ticket,
    });
    if (response.status === 200) {
      sessionStorage.setItem('jwt', response.data);
      return response.data;
    }
  } catch {
    throw new Error('Failed to fetch token');
  }
};
