import { useState, useEffect } from 'react';
import { useFetchData } from './hooks/useFetchData';
import { useFetchUserInfo } from './hooks/useFetchUserInfo';
import { handleLogout, fetchTicket, fetchToken } from './utils/authUtils';
import UserInfo from './components/UserInfo';
import DataTable from './components/DataTable';

export default function Home() {
  const [ticket, setTicket] = useState<string | null>(null);
  const [tokenData, setTokenData] = useState<string | null>(null);
  const [token, setToken] = useState<string | null>(null);

  // Initialize token from sessionStorage on the client side
  useEffect(() => {
    if (typeof window !== 'undefined') {
      const storedToken = sessionStorage.getItem('jwt');
      setToken(storedToken);
    }
  }, []);

  const { data, error: dataError } = useFetchData('http://localhost:3001/api/data', token);
  const { userInfo, error: userInfoError } = useFetchUserInfo(token);

  useEffect(() => {
    if (!token && !ticket) {
      fetchTicket()
        .then(setTicket)
        .catch(() => console.error('Failed to obtain ticket'));
    } else if (ticket) {
      const interval = setInterval(async () => {
        try {
          const newToken = await fetchToken(ticket);
          setTokenData(newToken);
          clearInterval(interval);
        } catch {
          // Handle polling errors if necessary
        }
      }, 5000);

      return () => clearInterval(interval);
    }
  }, [token, ticket]);

  useEffect(() => {
    if (tokenData) {
      if (typeof window !== 'undefined') {
        sessionStorage.setItem('jwt', tokenData);
      }
      window.location.reload();
    }
  }, [tokenData]);

  if (dataError || userInfoError) {
    return <div className="text-red-500">Error: {dataError || userInfoError}</div>;
  }

  if (!data) {
    if (ticket) {
      return (
        <div className="p-4">
          <h1 className="text-xl font-bold mb-4">Informacija prieinama tik patvirtinus savo tapatybÄ™</h1>
          <form
            action="https://test.epaslaugos.lt/portal/external/services/authentication/v2/"
            method="post"
            target="_blank"
            className="flex flex-col items-start">
            <input name="ticket" value={ticket} type="hidden" />
            <button
              type="submit"
              className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600 transition duration-200">
              Prisijungti per VIISP
            </button>
          </form>
        </div>
      );
    } else {
      return <div className="text-gray-500">Loading...</div>;
    }
  }

  return (
    <div className="p-4">
      <UserInfo name={userInfo.name} country={userInfo.country} />
      <DataTable data={data} />
      <button
        onClick={handleLogout}
        className="bg-red-500 text-white px-4 py-2 rounded hover:bg-red-600 transition duration-200 mt-4">
        Atsijungti
      </button>
    </div>
  );
}
